
import React, { Component } from 'react'
import { Card, Grid  } from 'semantic-ui-react';
import Link from 'next/link'
import Web3Container from '../lib/Web3Container'

class Details extends Component {
    state = {web3: null, contract: null, accounts: null}
    
    // static async getInitialProps(props) {  
    //     const { web3, contract, accounts } = props.query  
    
    //     console.log(web3)
    //     console.log(contract)    
    //     console.log(accounts)

    //     return  { web3, contract, accounts }
    // }

    async componentDidMount () {
        const { web3, contract, accounts } = this.props
    
        console.log(web3)
        console.log(contract)    
        console.log(accounts) 

        const name = await contract.methods.name().call();
        const symbol = await contract.methods.symbol().call();
        const totalSupply = await contract.methods.totalSupply().call();
        const currentAccount = accounts[0];
        const accountBalance = await contract.methods.balanceOf(currentAccount).call();
        const allowance = await contract.methods.allowance(currentAccount, accounts[1]).call();

        this.setState({ 
            web3, 
            contract, 
            accounts, 
            name, 
            symbol, 
            totalSupply, 
            currentAccount, 
            accountBalance,
            allowance
        }); 
    }
    renderCards() {
        const {
            name, 
            symbol, 
            totalSupply, 
            currentAccount, 
            accountBalance,
            allowance
        } = this.state;
     
        const items = [
          {
            header: name,
            meta: 'Name of Token Account',              
          },
          {
            header: symbol,
            meta: 'Token Symbol',          
          },
          {
            header: totalSupply,
            meta: 'Total Token Supply',           
          },
          {
            header: currentAccount,
            meta: 'User Account Address',
           
          },
          {
            header: accountBalance,
            meta: 'Current Account Balance',            
          },
          {
            header: allowance,
            meta: 'Allowance Granted to Spender',            
          }
        ];
    
        return <Card.Group items={items} />;
      }
    render() {
         
        return (
        <div>
            <h3>Account Details</h3>
            <Grid>
                <Grid.Row>
                    <Grid.Column width={20}>{this.renderCards()}</Grid.Column>            
                </Grid.Row> 
            </Grid>
        </div>
        )
    }    
}

export default () => (
    <Web3Container
      renderLoading={() => <div>Loading Dapp Page...</div>}
      render={({ web3, accounts, contract }) => (
        <Details accounts={accounts} contract={contract} web3={web3} />
      )}
    />
)


